



















































import { Component, Vue } from "vue-property-decorator";
import { Subject, from, of } from "rxjs";
import { ajax } from "rxjs/ajax";
import { map, mergeMap, reduce, catchError, tap, toArray, filter, pluck, distinctUntilChanged, debounceTime, take } from "rxjs/operators";
const numeral = require('numeral')
const moment = require('moment')

@Component
export default class Search extends Vue {
  stockdb: any
  symbols: any
  masterList: any

  searchSymbol$ = new Subject()
  addFavorite$ = new Subject()

  data() {
    return {
      httpMessage: '',
      masterList: [],
      symbols: [],
      gainers: [],
      losers: []
    }
  }

  created() {
    this.stockdb.getSymbols()
      .subscribe(
        (result: any) => {
          const data = result.data
          // this.symbols = data.slice(0, 100)
          this.masterList = data.slice()
        },
        (error: Error) => {
          console.log(error.message)
        }
      )
  }

  mounted() {
    this.$subscribeTo(this.searchSymbol$.pipe(
      pluck('event', 'target', 'value'),
      distinctUntilChanged(),
      debounceTime(250),
      mergeMap((input: string) => {
        this.symbols = []

        if (input === '') {
          return of([])
        }

        return from(this.masterList).pipe(
          filter((symbol: any) => {
            const s = symbol.symbol.toLowerCase()
            const n = symbol.name.toLowerCase()
            const i= input.toLowerCase()

            if (s === i) {
              return true
            }

            if (n.includes(i)) {
              return true
            }

            return false
          }),
          take(100),
          toArray()
        )
      }),
    ), (symbols: any) => {
      this.symbols = symbols
    })

    this.$subscribeTo(this.addFavorite$, (symbol) => {
      console.log('ok')
    })
  }
}
