







































import { Component, Vue } from 'vue-property-decorator';
import { from } from 'rxjs'
import { ajax } from "rxjs/ajax"
import { map, mergeMap, defaultIfEmpty, catchError } from 'rxjs/operators'

@Component
export default class Company extends Vue {
  symbol!: string
  isActive!: string

  data() {
    return {
      symbol: this.$route.params.symbol.toUpperCase(),
      isActive: 'is-hidden-mobile'
    }
  }

  toggleNav() {
    this.isActive = this.isActive === '' ? 'is-hidden-mobile': ''
  }

}
